import React, { useState } from "react";

import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import FormGroup from '@mui/material/FormGroup';
import Select from '@mui/material/Select';
import MenuItem from "@mui/material/MenuItem";
import CircularProgress from '@mui/material/CircularProgress';

import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";

import Grid from '@mui/material/Grid';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Switch from '@mui/material/Switch';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import IconButton from '@mui/material/IconButton';


import CampaignTable from './MyTable';
import ProTip from './ProTip';
import Settings from './Settings';
import Logs from './Logs';


import Papa from "papaparse";

const URL = ""

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      msg: "",
      password: "",
      selectedFile: undefined,
      isSelected: false,
      submitting: false,
      error: false,
      errorMsg: "",
      csvParsed: false,
      csvErrors: false,
      numbers: 0,
      variables: 0,
      smsSent: 0,
      smsErrors: 0,
      checkedSuccess: 0,
      formatErrors: 0,
      nonmobileNumbers: [],
      invalidNumbers: [],
      firstTimeSend: false,
      sendResultsArray: [],
      services_array: [],
      emptyNumbersArray: [],
      invalidNumbersCheck: false,
      checkCompleted: false,
      sendCompleted: false,
      getServicesCompleted: false,
      sender: "",
      csvData: undefined,
      radioSelection: "SMS",
      editIdx: -1,
      carrierSwitch: false,
      msgServiceSwitch: false,  
      selectedService: "",
      showServicesLoadingIcon: false,
      showCheckLoadingIcon: false,
      showSendLoadingIcon: false,
      logs: "",
      extraRowsAdded: 0
      
    };
    this.onMsgChange = this.onMsgChange.bind(this);
    this.onPwdChange = this.onPwdChange.bind(this);
    this.onSenderChange = this.onSenderChange.bind(this);
    this.onFileChange = this.onFileChange.bind(this);
    this.onRadioChange = this.onRadioChange.bind(this);
    this.sendMsg = this.sendMsg.bind(this);
    this.checkNumbers = this.checkNumbers.bind(this);
    this.getData = this.getData.bind(this);
    this.onHeaderClick = this.onHeaderClick.bind(this);
    this.handleRemove = this.handleRemove.bind(this);
    this.startEditing = this.startEditing.bind(this);
    this.stopEditing = this.stopEditing.bind(this);
    this.handleEditing = this.handleEditing.bind(this);
    this.handleCarrierSwitch = this.handleCarrierSwitch.bind(this);
    this.handleMsgServiceSwitch = this.handleMsgServiceSwitch.bind(this);
    this.loadMessagingServices = this.loadMessagingServices.bind(this);
    this.handleSelectService = this.handleSelectService.bind(this);
    this.updateLogs = this.updateLogs.bind(this);
    this.checkEmptyNumbers = this.checkEmptyNumbers.bind(this);
    this.getMessageStatus = this.getMessageStatus.bind(this);
  }

  getMessageStatus = () => {

    this.setState({ submitting: true, error: false });

    console.log(`Current Send Results Array: ${JSON.stringify(this.state.sendResultsArray)}`)

    const data = {
      'sendResultsArray':this.state.sendResultsArray
    }

    fetch(`${URL}get-message-status`, {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(`Data returned: ${JSON.stringify(data)}`);
        if (!data.status) {
          throw data.message;
        }
        this.setState({
          submitting: false,
          sendResultsArray: data.data.sendResultsArray
        });
        this.setState({logs: this.updateLogs("getMessageStatus")})
      })
      .catch((error) => {
        console.log(error);
        this.setState({ submitting: false, error: true, errorMsg: error});
        //this.setState({logs: this.updateLogs("getMessageStatusError")})
      });

  }

  checkEmptyNumbers = () => {
    for(let i = 0; i < this.state.csvData.length; i++){
      if(this.state.csvData[i]["Number"].length === 0){
        this.setState({emptyNumbersArray: this.state.emptyNumbersArray.push(i)})
      }
    }
    if(this.state.emptyNumbersArray.length > 0){
      this.setState({logs: this.updateLogs("emptyNumbersError")});
      this.setState({emptyNumbersArray: []})
      return true;
    }
    else {
      return false;
    }
  }

  updateLogs = (source) => {
    let date = new Date(Date.now());
    date.setUTCSeconds(0);
    let checkLog = ""
    checkLog = `Date: ${date.toString()}`
    let arr = []

    if(source === "check"){
      checkLog += `\nAction: Checked Number Format`
      checkLog += "\nTotal numbers having valid format: " + this.state.checkedSuccess
      this.state.invalidNumbers.length > 0 ? checkLog += "\n!!! Total invalid numbers found: " + this.state.invalidNumbers.length + " ==> Check rows: " + JSON.stringify(this.state.invalidNumbers) + "\n==> Please fix these errors to be able to send messages" : checkLog += "\nNo invalid numbers found"
      if(this.state.carrierSwitch){
        checkLog += "\nAction: Checked Carrier Type"     
        this.state.nonmobileNumbers.length > 0 ? checkLog += "\n!!! Total non-mobile numbers found: " + this.state.nonmobileNumbers.length + " ==> Check rows: " + JSON.stringify(this.state.nonmobileNumbers) : checkLog += "\nNo non-mobile numbers found"      
      }
      
    }
    else if(source === "checkError"){
      checkLog += `\nAction: Checked Numbers`
      checkLog += `\n!!! Error: ${this.state.errorMsg}`
    }

    else if(source === "sendError"){
      checkLog += `\nAction: Send Messages`
      checkLog += `\n!!! Error: ${this.state.errorMsg}`
    }

    else if(source === "send"){
      checkLog += `\nAction: Send Messages`
      checkLog += `\nNumber of messages created successfully: ${this.state.smsSent}`
      if(this.state.sendResultsArray.length > 0){
        this.state.sendResultsArray.forEach((message,index) => {
          if (message.status === "undelivered" || message.status === "failed")
            arr.push(index)
        })
      }
      arr.length > 0 || this.state.smsErrors > 0 ? checkLog += `\n!!! Total numbers sent with errors: ${this.state.smsErrors} ==> Check rows: ${JSON.stringify(arr)}` : checkLog += "\nNo errored messages found"
      checkLog += `\nPLEASE CLICK ON 'CHECK STATUS' FOR FINAL STATUS OF SENT MESSAGES`
    }

    else if(source=== "emptyNumbersError"){
      checkLog += `\n!!! Error: Numbers can't be empty ==> Check rows: ${JSON.stringify(this.state.emptyNumbersArray)}`
    }

    else if(source=== "getMessageStatus"){
      checkLog += '\nCurrent Messages Status:';
      let delivered = []
      let failed = []
      let in_progress = []
      for(let i = 0; i < this.state.sendResultsArray.length; i++){
        if(this.state.sendResultsArray[i].status === "delivered")delivered.push(i)
        else if(this.state.sendResultsArray[i].status === "undelivered")failed.push(i)
        else if(this.state.sendResultsArray[i].status === "failed")failed.push(i)
        else in_progress.push(i)
      }
      checkLog += "\n===> Delivered: " + JSON.stringify(delivered)
      checkLog += "\n===> Failed/Undelivered: " + JSON.stringify(failed)
      checkLog += "\n===> In-Progress: " + JSON.stringify(in_progress)

    }

    return this.state.logs ? checkLog + "\n\n" + this.state.logs : checkLog
    
  }

  addRow = () => {
    let cols = Object.keys(this.state.csvData[0])
    let new_row = {}
    for (let i = 0; i < cols.length; i++){
      new_row[cols[i]] = ""
    }
    this.state.csvData.unshift(new_row)
    this.setState({csvData: this.state.csvData, extraRowsAdded: this.state.extraRowsAdded + 1})
  }

  startEditing = i => {
    this.setState({ editIdx: i });
  };

  handleCarrierSwitch = (carrierSwitchFromChild) => {
    this.setState({carrierSwitch: carrierSwitchFromChild})
  }

  handleSelectService = (event) => {
    this.setState({selectedService: event.target.value})
  }

  handleMsgServiceSwitch = (msgServiceSwitchFromChild) => {
    this.setState({msgServiceSwitch: msgServiceSwitchFromChild})
    if(!this.state.msgServiceSwitch && !this.services_array){
      this.loadMessagingServices()
    }
    else{
      this.setState({getServicesCompleted: false, services_array: [], selectedService: ""})
    }
  }

  loadMessagingServices(){
    this.setState({ submitting: true, getServicesCompleted: false, error: false, showServicesLoadingIcon: true });
    fetch(`${URL}get-services`, {
      method: "POST",
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(`Data returned: ${data}`);

        this.setState({
          submitting: false,
          showServicesLoadingIcon: false,
          getServicesCompleted: true,
          services_array: data.data.services_array
        });
      })
      .catch((error) => {
        console.log(error);
        this.setState({ submitting: false, error: true, errorMsg: error, showSendLoadingIcon: false });
      });
  }

  stopEditing = () => {
    this.setState({ editIdx: -1 });
  };

  handleEditing = (e, name, i) => {
    const { value } = e.target;
    this.setState(state => ({
      csvData: state.csvData.map(
        (row, j) => (j === i ? { ...row, [name]: value } : row)
      )
    }));
  };

  handleRemove = i => {
      this.setState(state => ({
        csvData: state.csvData.filter((row, j) => j !== i),
        sendResultsArray: state.sendResultsArray.filter((row, j) => j !== i)
      }));
    };

  onMsgChange(event) {
    this.setState({
      msg: event.target.value,
    });
  }

  onPwdChange(event) {
    this.setState({
      password: event.target.value,
    });
  }

  onSenderChange(event) {
    this.setState({
      sender: event.target.value,
    });
  }

  onFileChange(event) {
    if (event.target.files.length > 0) {
      this.setState({
        isSelected: true,
        selectedFile: event.target.files[0],
        sendCompleted: false,
        checkCompleted: false,
        error: false,
      });

      Papa.parse(event.target.files[0], {
        header: true,
        complete: this.getData,
      });
    }
  }

  onRadioChange(event){
    this.setState({
      radioSelection: event.target.value
    })

    
  }

  onHeaderClick(event){
    this.setState({
      msg: this.state.msg+ '[' + event + ']',
    });
  }

  checkNumbers(event){
    event.preventDefault();

    if(this.checkEmptyNumbers())return;

    this.setState({ submitting: true, checkCompleted: false, error: false, showCheckLoadingIcon: true });


    if(this.state.sendResultsArray.length === 0){
      console.log("constructing array")
      let arr = []
      let obj = {}
      for(let i = 0; i < this.state.csvData.length; i++){
        obj["sid"] = ""
        obj["error"] = {}
        obj["error"]["errorCode"] = ""
        obj["error"]["errorMessage"] = ""
        obj["error"]["errorLink"] = ""
        obj["status"] = ""
        arr.push(obj)
      }
      console.log(JSON.stringify(arr))
      this.setState({sendResultsArray: arr})
    }

    const data = {
      'csvData':this.state.csvData,
      'radioSelection': this.state.radioSelection,
      'carrierSwitch': this.state.carrierSwitch,
      'password': this.state.password
    }

    fetch(`${URL}check-numbers`, {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(`Data returned: ${data}`);
        if (data.passwordError){
          throw data.passwordError
        }
        if (!data.status) {
          throw data.message;
        }
        this.setState({
          submitting: false,
          checkedSuccess: data.data.checkedSuccess,
          formatErrors: data.data.formatErrors,
          nonmobileNumbers: data.data.nonmobileNumbers,
          invalidNumbers: data.data.invalidNumbers,
          invalidNumbersCheck: true,
          checkCompleted: true,
          showCheckLoadingIcon: false
        });
        this.setState({logs: this.updateLogs("check")})
      })
      .catch((error) => {
        console.log(error);
        this.setState({ submitting: false, error: true, errorMsg: error, showCheckLoadingIcon: false });
        this.setState({logs: this.updateLogs("checkError")})
      });
  }

  sendMsg(event) {
    event.preventDefault();

    if(this.checkEmptyNumbers())return;
    
    this.setState({ submitting: true, sendCompleted: false, error: false, showSendLoadingIcon: true, firstTimeSend: true});

    let sender = this.state.selectedService !== "" ? this.state.selectedService : this.state.sender
    let isMsgService = this.state.selectedService !== "" ? true : false



    console.log(`CSV Data: ${JSON.stringify(this.state.csvData)}`)
    console.log(`Current Results Array: ${JSON.stringify(this.state.sendResultsArray)}`)

    const data = {
      'textmsg':this.state.msg,
      'sender': sender,
      'csvData':this.state.csvData,
      'channel':this.state.radioSelection,
      'isMsgService': isMsgService,
      'password': this.state.password,
      'sendResultsArray': this.state.sendResultsArray
    }


    fetch(`${URL}send`, {
      method: "POST",
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        if (data.passwordError){
          throw data.passwordError
        }
        if (!data.status) {
          throw data.message;
        }
        this.setState({
          submitting: false,
          smsSent: data.data.sentSuccess,
          smsErrors: data.data.sentErrors,
          sendCompleted: true,
          showSendLoadingIcon: false,
          sendResultsArray: data.data.sendResultsArray
        });
        this.setState({logs: this.updateLogs("send")})
      })
      .catch((error) => {
        console.log(error);
        this.setState({ submitting: false, error: true, errorMsg: error, showSendLoadingIcon: false });
        this.setState({logs: this.updateLogs("sendError")})
      });
  }

  getData(result) {
    if (result.errors.length > 0) {
      this.setState({
        csvErrors: true,
        csvParsed: false,
        isSelected: false,
      });
    } else {
      this.setState({
        csvParsed: true,
        csvErrors: false,
        csvData: result.data,
        numbers: result.data.length,
        variables: result.meta.fields.length,
      });
    }
  }

  render() {
    return (

      <Container style={{
        minWidth: "100%",
        height: "100vh",
      }}>
      <Box sx={{ my: 4 }}>
        <Box sx={{ p: 1 }}></Box>
        <Grid container spacing={3}>
          <Grid item xs={4}>
            <FormControl fullWidth className="mb-3">
              <Button variant="contained" margin="normal" component="label">
                {this.state.isSelected ? this.state.selectedFile.name : "Upload file"}{" "}
                <input
                  type="file"
                  name="myFile"
                  accept=".csv"
                  onChange={this.onFileChange}
                  hidden
                />
              </Button>

              {this.state.csvParsed ? (
                <Box mt={1}>
                  <FormLabel>
                    <b>Total Rows Count:</b> {this.state.csvData.length}<br/>
                    <b>Total Columns Count:</b> {this.state.variables}
                  </FormLabel>{" "}
              </Box>
            ) : (
              ""
            )}

            
            {this.state.csvErrors ? (
              <Box mt={1}>
                <FormLabel>
                  Errors parsing the CSV file <b>{this.state.selectedFile.name}</b>.{" "}
                </FormLabel>{" "}
              </Box>
            ) : (
              ""
            )}

              <Box mt={2}>
              <Typography variant="h5" component="h1" gutterBottom>
                Channel Type
              </Typography>
              <RadioGroup aria-label="channel" name="channel" value={this.state.radioSelection} onChange={this.onRadioChange}>
                <FormControlLabel value="SMS" control={<Radio />} label="SMS" />
                <FormControlLabel value="Whatsapp" control={<Radio />} label="Whatsapp" />
              </RadioGroup>
            </Box>

             <FormControl component="fieldset" margin="normal">
             <Typography variant="h5" component="h1" gutterBottom>
                Sender and Message
              </Typography>
              <ProTip />

              {this.state.services_array.length > 0 ?
                (
                  <Select defaultValue={this.state.services_array[0].sid}
                  value={this.selectedService}
                  onChange={this.handleSelectService}
                  >
                  {this.state.services_array.map((service,index) =>
                    <MenuItem key={index} value={service.sid}>{service.name}</MenuItem>
                  )}
                  </Select>

                )
                :
                (<TextField
                  id="sender"
                  name="sender"
                  value={this.state.sender}
                  onChange={this.onSenderChange}
                  margin="normal"
                  required
                  placeholder="Sender"
                />)
              }

              <TextField
                id="msgContent"
                name="myMsg"
                value={this.state.msg}
                onChange={this.onMsgChange}
                rows={3}
                margin="normal"
                multiline
                required
                placeholder="Message"
              />

              <TextField
                id="password"
                name="password"
                type="password"
                value={this.state.password}
                onChange={this.onPwdChange}
                margin="normal"
                required
                placeholder="Password"
              />

          
              <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
              <Grid item xs={6}>
              <Button fullWidth
                variant="contained"
                onClick={this.checkNumbers}
                disabled={
                  !this.state.isSelected ||
                  this.state.submitting
                }
                margin="normal"
                component="label"
              >
                Check Numbers
              </Button>
              </Grid>
              <Grid item xs={6}>
              <Button fullWidth
                variant="contained"
                onClick={this.sendMsg}
                disabled={
                  !this.state.isSelected ||
                  this.state.submitting ||
                  this.state.msg.length == 0 ||
                  !this.state.invalidNumbersCheck ||
                  this.state.invalidNumbers.length > 0 ||
                  (this.state.sender.length == 0 && this.state.selectedService === "")
                }
                margin="normal"
                component="label"
              >
                Send messages
              </Button>
              </Grid>
              <Grid item xs={6}>
              {
                this.state.showCheckLoadingIcon ?

                (<>
                  
                  <Typography variant="h6"gutterBottom>
                    Checking Numbers...
                    <CircularProgress size={20}/>
                  </Typography>
                  

                </>) : ""
              }
              {
                this.state.showSendLoadingIcon ?

                (<>
                  
                  <Typography variant="h6"gutterBottom>
                    Sending Messages...
                    <CircularProgress size={20}/>
                  </Typography>
                  
                </>) : ""
              }
              </Grid>
              </Grid>
              </FormControl>

              <Settings 
                channelRadioButton={this.state.radioSelection} 
                carrierTypeSwitchCallback={this.handleCarrierSwitch}
                msgServiceSwitchCallback={this.handleMsgServiceSwitch}
                showServicesLoadingIcon={this.state.showServicesLoadingIcon}
              />
              

              

               
          </FormControl>
            </Grid>
          {this.state.csvParsed ? (
              <Grid item xs={8} >
                <Logs
                  logs={this.state.logs}
                 />
                 <Box mt={2}> 
                  <CampaignTable 
                    json={this.state.csvData} 
                    onHeaderClick={this.onHeaderClick} 
                    handleRemove={this.handleRemove}
                    startEditing={this.startEditing}
                    editIdx={this.state.editIdx}
                    stopEditing={this.stopEditing}
                    handleEditing={this.handleEditing}
                    nonmobileNumbers={this.state.nonmobileNumbers}
                    invalidNumbers={this.state.invalidNumbers}
                    sendResultsArray={this.state.sendResultsArray}
                    addRow={this.addRow}
                    getMessageStatus={this.getMessageStatus}
                    firstTimeSend={this.state.firstTimeSend}
                  />
                </Box>
              </Grid>

            ) : (
              ""
            )}

          

        </Grid>

      </Box>
    </Container>

    );
  }
}

export default App;
