import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TablePagination from '@mui/material/TablePagination';
import TableFooter from '@mui/material/TableFooter';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';

import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import CheckIcon from '@mui/icons-material/Check';
import IconButton from '@mui/material/IconButton';
import ErrorIcon from '@mui/icons-material/Error';
import WarningIcon from '@mui/icons-material/Warning';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import Tooltip from '@mui/material/Tooltip';
import TextField from '@mui/material/TextField';
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";

const in_progress_statuses = ["accepted", "queued", "sent", "sending"]
const failed_statuses = ["undelivered", "failed"]

class CampaignTable extends React.Component{

  constructor(props) {
    super(props);
    this.addRowClick = this.addRowClick.bind(this);
  }

 
  changeHandler(event) {
    if (typeof this.props.onHeaderClick === 'function') {
        this.props.onHeaderClick(event);
   }
  }

  deleteRow(event) {
    if (typeof this.props.handleRemove === 'function') {
      this.props.handleRemove(event)
    }
  }

  startEditing(event) {
    if (typeof this.props.startEditing === 'function') {
      this.props.startEditing(event)
    }
  }

  stopEditing() {
    if (typeof this.props.stopEditing === 'function') {
      this.props.stopEditing()
    }
  }

  handleEditing(e, val, i) {
    if (typeof this.props.handleEditing === 'function') {
      this.props.handleEditing(e, val, i)
    }
  }

  addRowClick() {

    if (typeof this.props.addRow === 'function') {
      this.props.addRow()
    }
  }

  getMessageStatus(){
    if (typeof this.props.getMessageStatus === 'function') {
      this.props.getMessageStatus()
    }
  }

  shouldShowIcon(i){
    if ((this.props.nonmobileNumbers && this.props.nonmobileNumbers.includes(i)) ||
        (this.props.invalidNumbers && this.props.invalidNumbers.includes(i)) ||
        (this.props.sendResultsArray && this.props.sendResultsArray.length > 0 && this.props.sendResultsArray[i]["status"] !== "")
    )
      return true;
    else
      return false;
  }

  showRelevantIcon(i){
    let title, color;
    let icon;
    let interactive = false;
    let renderTitleJSX = false;

    if (this.props.nonmobileNumbers && this.props.nonmobileNumbers.includes(i)){
      title = "We could not identify this number as a valid mobile number. Ensure you don't send SMS to non-mobile numbers"
      color = "orange"
      icon = "WarningIcon"
      interactive = false

    }
    else if (this.props.invalidNumbers && this.props.invalidNumbers.includes(i)){
      title = "This number was marked as invalid, does it have the correct number of digits?"
      color = "red"
      icon = "ErrorIcon"
      interactive = false

    } 

    if (this.props.sendResultsArray && this.props.sendResultsArray.length > 0 && this.props.sendResultsArray[i]["status"] === "delivered"){
      title = "Message marked as Delivered - It will be excluded from further 'Send Messages' actions"
      color = "green"
      icon = "CheckCircleIcon"
      interactive = false
    }

    else if (this.props.sendResultsArray && this.props.sendResultsArray.length > 0 && failed_statuses.includes(this.props.sendResultsArray[i]["status"])){
      
      color = "red"
      icon = "ErrorIcon"
      interactive = true
      renderTitleJSX = true
    }

    else if (this.props.sendResultsArray && this.props.sendResultsArray.length > 0 && in_progress_statuses.includes(this.props.sendResultsArray[i]["status"])){
      
      title = "Message Successfuly Created - Check Status periodically for final message status. If Status does not change in a few minutes, check Twilio Logs"
      color = "grey"
      icon = "CheckCircleIcon"
      interactive = false
    } 

    return {title, color, icon, interactive, renderTitleJSX}

  }


  
  render() {
    var data = this.props.json;
    var editIdx = this.props.editIdx;

    return (
      <>
      <Typography variant="h5" component="h1" gutterBottom>
        User Data
      </Typography>
      {/*<Button variant="outlined" startIcon={<AddIcon />} onClick = {() => this.addRowClick()}>Add Row</Button>*/}
      <TableContainer component={Paper} style={{ maxHeight: '70vh', border: "1px solid rgba(0,0,0,0.2)", padding: 4 }}>
        <Table stickyHeader={true} sx={{ tableLayout: "auto"}} style={{"maxHeight": "20vh"}} >
          <TableHead>
            <TableRow>
              <TableCell key={`th-id`} style={{color: 'grey'}}>ID</TableCell>
              {Object.keys(data[0]).map((col) => (
                   <>
                   <TableCell key={col} style={{cursor: 'pointer'}} onClick={ () => this.changeHandler(col)}>{col}</TableCell>
                   
                   </>
              ))}
              <TableCell key={`th-ed`} style={{color: 'grey'}}>Edit</TableCell>
              <TableCell key={`th-del`} style={{color: 'grey'}}>Delete</TableCell>
              <TableCell key={`th-st`}><Button variant="outlined" disabled={!this.props.firstTimeSend} onClick = {() => this.getMessageStatus()} >Get Status</Button></TableCell>
            </TableRow>
          </TableHead>

          <TableBody>

          {data.map((row,i) => (

            <TableRow
                key={`tr-${i}`}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                  <TableCell key={`tc-id-${i}`}>
                              {i}
                              </TableCell>


                  {Object.keys(row).map((col) => (

                              
                               <TableCell key={`tc-${col}-${i}`}>
                                
                               { editIdx === i ?
                                  (
                                    <div>
                                    <TextField
                                      name={row[col]}
                                      onChange={e => this.handleEditing(e, col, i)}
                                      value={row[col]}
                                    />
                                    </div>
                                  )
                                  :
                                  (
                                    row[col]
                                  )
                                }
                               </TableCell>
                          ))}


                { editIdx === i ?

                  (<TableCell key={`tc-se-${i}`} onClick={ () => this.stopEditing()}>
                     <Tooltip title="Stop editing">
                      <IconButton >
                        <CheckIcon />
                      </IconButton>
                    </Tooltip>   
                    </TableCell>) 

                  :

                   (<TableCell key={`tc-ed-${i}`} onClick={ () => this.startEditing(i)}>
                     <Tooltip title="Edit this row">
                      <IconButton >
                        <EditIcon />
                      </IconButton>
                    </Tooltip>   
                    </TableCell>) 
                }
                 
                 <TableCell key={`tc-de-${i}`} onClick={ () => this.deleteRow(i)}>
                   <Tooltip title="Delete this row">
                    <IconButton >
                      <DeleteIcon />
                    </IconButton>
                  </Tooltip>   
                  </TableCell>  



                  {this.shouldShowIcon(i) ?

                    (<TableCell key={`tc-ic-${i}`}>
                     <Tooltip
                     title={this.showRelevantIcon(i).renderTitleJSX ?
                        (<>
                        Error code: {this.props.sendResultsArray[i]["error"].errorCode}<br/>
                        {this.props.sendResultsArray[i]["error"].errorLink ? (<>For more information please click <a href={this.props.sendResultsArray[i]["error"]["errorLink"]} target="_blank">HERE</a></>) : (<>For more information please click <a href={"https://twilio.com/docs/api/errors/" + this.props.sendResultsArray[i]["error"].errorCode} target="_blank">HERE</a></>)}   
                        </>)
                      : this.showRelevantIcon(i).title} 
                     interactive={this.showRelevantIcon(i).interactive ? 1 : 0}>
                      <IconButton >
                        {this.showRelevantIcon(i).icon === "WarningIcon"? <WarningIcon style={{color: this.showRelevantIcon(i).color}} /> : "" }
                        {this.showRelevantIcon(i).icon === "ErrorIcon"? <ErrorIcon style={{color: this.showRelevantIcon(i).color}} /> : "" }
                        {this.showRelevantIcon(i).icon === "CheckCircleIcon"? <CheckCircleIcon style={{color: this.showRelevantIcon(i).color}} /> : "" }
                      </IconButton>
                    </Tooltip>   
                    </TableCell>)
                    :
                    (<></>)
                  } 
                  
              </TableRow>  
          ))}
          </TableBody>
        </Table>
      </TableContainer>
      </>
   
    );
  }
}

export default CampaignTable;

